@import (less) "color.less";

@fontSizeTitle: 0.875em;

.panel {
    // border: 1px solid @green;
    box-shadow: none;
    .panel-open {
      background-color: @lightest_gray !important;
      border: 0.0625em !important;
      border-color: @gray;
      margin-top: 0.625em;
    }
}
.panel-heading {
    text-align: center;
    border: 0.0625em solid @green !important;
    box-shadow: none;
    color: #258900;
    background-color: @white;
    padding: 1em 1.250em 1em 1.250em !important;
    height: 3.125em;
    width: 18.75em;
    border-radius: 1.5625em;
    &:hover {
      cursor: pointer;
      background-color: @green !important;
      .panel-title {
        color: @white;
      }
      .triangolo-top-accordion {
        border-color: transparent transparent @white transparent;
      }
      .triangolo-bottom-accordion {
          border-color: @white transparent transparent transparent;
      }
    }
    &.panel-heading-round{
      background-color: @green !important;
      .panel-title {
        color: @white;
      }
      .triangolo-top-accordion {
        border-color: transparent transparent @white transparent;
      }
      .triangolo-bottom-accordion {
          border-color: @white transparent transparent transparent;
      }
      // border-radius: 2px 2px 2px 2px !important;
    }
    .panel-title {
        color: @green;
        font-weight: bold;
        font-size: 0.750em !important;
        text-transform: uppercase;
    }
    .accordion-arrow {
        color: black;
        position: relative;
        font-size: 1.625em !important;
        margin-top: 1.156em;
    }
    .triangolo-top-accordion {
        border-style: solid;
        border-width: 0 0.4375em 0.4375em 0.4375em;
        border-color: transparent transparent @green transparent;
        display: inline-block !important;
        // margin-bottom: 5px;
        // position: absolute !important;
        // margin-left: -10px;
        // margin-top: 2px;
    }
    .triangolo-bottom-accordion {
        border-style: solid;
        border-width: 0.4375em 0.4375em 0 0.4375em;
        border-color: @green transparent transparent transparent;
        display: inline-block !important;
        // margin-bottom: 5px;
        // position: absolute !important;
        // margin-left: -10px;
        // margin-top: 2px;
    }
    .stringa{
        color: @white;
        font-size: 0.750em !important;
        margin-right: 1.875em;
        font-weight: bold;
        font-family: arial;
    }
    h4 {
        margin: 0;
        font-size: 1em;
    }
}

.panel-body {
    border: 0.0625em !important;
    box-shadow: none;
    background-color: @lightest_gray !important;
    // border-radius: 0px 0px 2px 2px !important;
}

.panel-collapse{
  width: 100%;
  .panel-body;
    max-height: 0;
    // transition: max-height 0.15s ease-out;
    overflow: hidden;
  &.in{
    max-height: 1000em;
    // transition: max-height 0.25s ease-in;
  }
}

.panelTitle{
      font-size: @fontSizeTitle;
      font-weight: bold;
      text-transform: uppercse;
}

.accordionIcon{
     font-size: 2em;
     position:absolute;
     top:0.5625em;
}


.nbp-panel-button {
  >.panel-heading {
      background-color: @white;
      position: relative;
      & .accordion-icon{
          .accordionIcon
      }
      >.panel-title{
       .panelTitle;
        >.title{
          padding: 0.125em 0.125em 0.125em 0;
        }
        .accordion-title-icon{
          padding: 0.125em 0.125em 0.125em 3.25em !important;
          margin-bottom:0.625em;
        }
    }
    >.panel-open {
      background-color: @lighter_green !important;
    }
  }
}

:host-context(.contrasto-on) {
    .panel {
        .panel-open {
          background-color: @white !important;
          color:@black;
          // border: 1px solid @black !important;
          .panel-title {
            color: @black;
          }
          .stringa {
            color:@black;
          }
          .accordion-arrow {
            color:@black;
          }
        }
    }
    .panel-heading {
        background-color: @black !important;
        &:hover {
          background-color: @white !important;
          color: @black;
          border: 0.0625em solid @black !important;
          .panel-title {
            color: @black;
          }
          .stringa {
            color:@black;
          }
          .accordion-arrow {
            color:@black;
          }
          .triangolo-top-accordion {
            border-color: transparent transparent @black transparent;
          }
          .triangolo-bottom-accordion {
              border-color: @black transparent transparent transparent;
          }
        }
        .panel-title {
            color: @white;
        }
        .accordion-arrow {
            color: @white;
        }
        .stringa{
            color: @white;
        }
    }

    .panel-body {
        background-color: @white !important;
        border-top: none !important;
        // border-right: 2px solid @black !important;
        // border-bottom: 2px solid @black !important;
        // border-left: 2px solid @black !important;
        color:@black;
    }
    .accordion-green-header {
        >.panel-heading {
          background-color: @black !important;
          &:hover {
            background-color: @white !important;
            color:@black;
            border: 0.0625em solid @black;
          }
        }
        >.panel-open {
          background-color: @white !important;
          color:@black;
          border:0.0625em solid @black !important;
        }
    }
    .triangolo-top-accordion {
      border-color: transparent transparent @white transparent;
    }
    .triangolo-bottom-accordion {
        border-color: @white transparent transparent transparent;
    }
}
